import React from 'react';
import styled from 'styled-components';
import Product from '@/db/typing';
import Divider from '@/components/Divider';
import dayjs from 'dayjs';
import Timer from '@/components/Timer';

const Sale = ({ data }: any) => {
  const time = dayjs(data.end).diff(dayjs(), 'second');

  return (
    <Wrapper>
      <CategoryInfo>
        SHOP {'>'} 검색 {'>'} 일반
      </CategoryInfo>
      <Divider marginTop="20px" marginBottom="20px" backgroundColor="#8993A2" />
      <ItemInfo>
        <ImageContainer>
          <img src={data.imageUrl} />
        </ImageContainer>
        <InfoContainer>
          <h1>{data.name}</h1>
          <PriceDiv>
            <p>현재가</p>
            <span>
              {new Intl.NumberFormat().format(data.price as number)}원
            </span>
            <CounterDiv>
              남은 시간 <Timer end={data.end} />
            </CounterDiv>
          </PriceDiv>
          <Divider marginBottom="16px" />
          <p>ㆍ찜{data.wishCount}ㆍ입찰수 12</p>
          <Divider marginTop="16px" marginBottom="16px" />
          <SellInfo>
            <p>
              ㆍ시작가{' '}
              <span>
                {new Intl.NumberFormat().format(data.price as number)}
              </span>
            </p>
            <p>
              ㆍ판매자 <span>{data.pStatus}</span>
            </p>
          </SellInfo>
        </InfoContainer>
      </ItemInfo>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  padding: 40px 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const CategoryInfo = styled.div`
  width: 100%;
  height: 100%;
  padding: 0px;
  display: flex;
  font-size: 16px;
  text-align: center;
  color: #a1a1a1;
`;

const ItemInfo = styled.div`
  width: 100%;
  height: 100%;
  padding: 0px;
  display: flex;
  font-size: 16px;
  text-align: center;
  color: #a1a1a1;
`;

const ImageContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  img {
    width: 400px;
    height: 400px;
    border-radius: 10px;
  }
`;

const InfoContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 12px 0px 0px 20px;
  h1 {
    text-align: left;
    font-size: 24px;
    color: #212121;
    line-height: 1.4;
    font-weight: 500;
  }
`;

const PriceDiv = styled.div`
  height: 80px;
  display: inline;
  align-self: flex-start;
  margin-top: 20px;
  color: #ff4c4c;
  font-size: 24px;
  p {
    color: #222;
    display: inline-block;
    margin-right: 12px;
    vertical-align: text-bottom;
    font-size: 16px;
    font-weight: 500;
  }
  span {
    font-size: 30px;
    vertical-align: top;
  }
`;

const CounterDiv = styled.div`
  display: inline-block;
  align-items: center;
  justify-content: center;
  margin-left: 20px;
  background-color: #ef6253;
  width: 200px;
  height: 36px;
  border-radius: 20px;
  text-align: center;
  font-size: 14px;
  color: #fff;
  vertical-align: middle;
  p {
    margin-top: 8px;
    color: #fff;
  }
`;

const SellInfo = styled.div`
  display: flex;
  flex-direction: column;
  p {
    width: 150px;
    text-align: left;
  }
  p + p {
    margin-top: 16px;
  }
  span {
    color: #222;
    margin-left: 8px;
  }
`;

export default Sale;
